.footer {
  background-color: rgba($color-primary-light, 0.3);

  padding: 7rem 0;
  font-size: 1.4rem;
  color: $color-white;

  &__logo-box {
    text-align: center;
    margin-bottom: 8rem;
  }

  &__logo {
    width: 15rem;
    height: auto;
  }

  &__navigation {
    border-top: 5px soild $color-primary-light;
    padding-top: 2rem;
    display: inline-block;
  }

  &__list {
    list-style: none;
  }

  &__item {
    display: inline-block;

    &:not(:last-child) {
      margin-right: 1.5rem;
    }
  }

  &__link {
    &:link,
    &:visited {
      color: $color-white;
      // background-color: rgba($color-primary-light, 0.4);
      text-decoration: none;
      text-transform: uppercase;
      display: inline-block;
      transition: all 0.2s;
    }

    // &:hover,
    // &:active {
    //   color: $color-primary-dark;
    //   box-shadow: 0 1rem 2rem rgba($color-black, 0.4);
    //   transform: rotate(5deg) scale(1.3);
    // }
  }

  &__copyright {
    border-top: 1px solid $color-white;
    padding-top: 2rem;
    width: 80%;
    float: right;

    @include respond(tab-port) {
      width: 100%;
      float: none;
    }
  }
}
